// colors
$color: #EFEFEF; // EFEFEF
$background: #232428; // 1E1F22
// pallette
$accent: #f7d955; //FFE349
$itemBackground: #232428;
$transparency: 0.5;

//font
$fonts: 'Space Grotesk', 'iA Writer', -apple-system, sans-serif;
$rem: 18px;
$mobileRem: 17px; // 17px
$title1: 1.3rem; 

$headWeight: 600; // bold

// margins
$sideSize: 3rem;
$smallSideSize: 2.2rem; // 2.5rem
$margin: 0.5rem;

$itemHeight: 24rem;
$borderWidth: 1.5px;
//
$break: 750px; //850px
//


html, body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

body, body * {
    box-sizing: border-box;
}

:root {
    font-size: $rem;
    @media (max-width: $break) {
        font-size: $mobileRem;
    }
}

body {
    background: $background;
    color: $color;
    // typography
    font-size: 1rem;
    font-family: $fonts;
    font-weight: normal; //500
    letter-spacing: 0.05em; 
    line-height: 1.7em;
}


// --------
//  STYLES
// --------

h1,h2,h3,h4,h5 {
    font-weight: $headWeight;
}
h1, h2 {
    font-size: 1.6rem;
    line-height: 1.3em;
}
p {
    letter-spacing: 0.03em;
    margin: 2rem 0;
}

a {
    display: block;
    text-decoration: none;
    color: $color;
}
.link {
    display: inline-block;
    //display: block;
    color: $accent;
    opacity: 0.9;

    &:before {
        content: "";
        display: inline-block;
        background: url('/assets/images/arrow-right.svg') no-repeat;
        width: 25px;
        height: 15px;
        margin: 0 8px 0 0;
        opacity: 0;
        transition: all .3s;
    }
    margin-left: -33px !important;

    transition: all .3s;
    &:hover {
        transform: translateX(33px);
        opacity: 1;
        &:before {
            opacity: 1;
        }
    }
}


img {
    object-fit: cover;
    margin: 0;
}
p img, p picture {
    width: 100%;
    margin: $margin*2 0 -0.6rem 0;
}
label {
    display: block;
    width: 100%;
    padding: 0.7rem 1rem 0.4rem 1rem;
    background: $itemBackground;
    opacity: $transparency;
    border: solid rgba(200, 200, 220, 0.2) $borderWidth;
    border-width: 0 $borderWidth $borderWidth $borderWidth;
    margin: 0;
}

video {
    width: 100%;
    height: auto;
    display: block;
    margin: $margin*2 0 0 0;
    padding: 0;
}

strong {
    color: $accent;
    font-weight: bold;
}
em {
    font-style: normal;
    color: $accent;
}


// --------
//   HERO
// --------

#hero {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -999;
    opacity: 0.5;

    img, picture {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        object-fit: cover;
        transition: all .8s;
    }

    @media (max-width: 500px) {
        height: 105vh;
    }
}
.hidden {
    opacity: 0;
}

.hero-title {
    margin: calc(50vh) 0 0 0;
    transform: translateY(-50%);
    text-align: center;
    font-weight: 400;
    font-size: $title1;
}
#role {
    color: $accent;
}

.arrow {
    text-align: center;
    font-size: $title1;
    
    margin: 5rem 0;

    opacity: $transparency;
    transition: all .3s;
    &:hover {
        opacity: 0.9;
    }

    p {
        margin: 0;
    }

    @media (max-width: $break) {
        font-size: 1.2rem;
        img {
            transform: scale(0.8);
        }
    }
}
.hero-arrow {
    margin: calc(50vh - 140px) 0 200px 0;
    &:hover {
        transform: translateY(1rem);
    }
}
.back-arrow {
    img {
        transform: rotate(90deg);
        transition: all .3s;
    }
    &:hover img {
        transform: translateX(-1rem) rotate(90deg);
    }
}


// ---------
//  SIDEBAR
// ---------

#sidebar-left {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: $sideSize;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    writing-mode: vertical-rl;
    padding: $margin*2 0;
    text-transform: lowercase;

    h2 {
        font-size: $title1;
        font-weight: normal;
        margin: 0;
        margin-bottom: $margin*2;
        height: auto;
        flex-shrink: 0;
    }
    span {
        display: block;
        width: $borderWidth; // 2px
        height: 100%;
        margin: 0;
        background: $color;
        opacity: $transparency;
    }

    transition: all .3s;
    &:hover {
        transform: translateX($margin);
    }

    @media (max-width: $break) {
        width: $smallSideSize;

        h2 {
            font-size: 1.2rem;
        }
    }
}
#sidebar-right {
    @extend #sidebar-left;

    left: unset;
    right: 0 !important;
    top: unset;
    bottom: 0 !important;

    h2 {
        margin: 0;
        margin-top: $margin * 2;
    }
    &:hover {
        transform: translateX(-$margin);
    }
}


// -----------
//  CONTAINER
// -----------

section {
    display: block;
    width: 100%;
}

.container {
    margin: 0;
    padding: 0 $sideSize;
    @media (max-width: $break) {
        padding: 0 $smallSideSize;
    }
}

.small-container {
    margin: 0 auto;
    margin-top: calc(50vh - 3rem);
    max-width: 800px;

    :first-child {
        margin: 0 0;
    }
    h1 {
        margin-top: $margin;
        font-size: 2rem;
        color: $accent;
    }
}
.contact-container {
    margin: 5rem auto;
    h2 {
        color: $accent;
    }
}

p > a {
    @extend .link;
}


// -------
//  ITEMS
// -------

.item {

    display: block;
    width: 100%;
    height: $itemHeight;
    overflow: hidden;
    margin: 1.5rem 0; //4em 0

    padding: 0;
    z-index: 20;

    box-sizing: border-box;
    border: solid rgba(red($color), green($color), blue($color), 0.15) $borderWidth;
    outline: none;

    img {
        position: relative;
        z-index: -1;
        width: 100%;
        height: 100%;
        opacity: 1;
        transition: all .3s;
    }
    &:hover img {
        transform: scale(1.05, 1.05);
    }

    transition: all .2s;
    &:hover {
        border: solid rgba(red($color), green($color), blue($color), 0.5) $borderWidth;
    }

    .disc {
        box-sizing: border-box;
        display: flex;
        flex-grow: 1;
        flex-flow: row wrap;
        background: $itemBackground;
        width: 20rem; //24rem
        z-index: 1;
        height: $itemHeight;
        padding: $margin * 2;
        margin: 0;
        margin-top: calc(#{-$itemHeight} - 0.55rem);

        * {
            width: 100%;
        }

        // styles
        .disc-title {
            align-self: flex-start;
            h2 {
                display: block;
                color: $accent;
                margin-top: $margin * 0.5;
                align-self: flex-start;
            }
            p {
                margin: 0;
                align-self: flex-start;
            }
        }

        h5 {
            opacity: $transparency;
            align-self: flex-end;
        }

        // hug top and bottom
        :first-child {
            margin-top: 0;
        }
        :last-child {
            margin-bottom: -0.5rem;
        }
    }

    @media (max-width: $break) {
        height: 80vh;


        .disc {
            width: 100%;
            height: $itemHeight * 0.4;
            margin-top: -$itemHeight * 0.4;

            :last-child {
                margin-bottom: 0;
            }
        }
    }

}


// ------
//  SWUP
// ------
.transition-swup {
    transition: 0.4s;
    opacity: 1;
}
html.is-animating .transition-swup {
    opacity: 0;
}